export async function getEmner(updateEmner) {
    emner = await axios.get("https://grades.no/api/v2/courses/")
        .then(
            res => {
                console.log(JSON.stringify(res.data))
                return res.data.map(emne => {

  
                    return {
                    navn: emne.norwegian_name,
                    kode: emne.code, 
                    snitt: emne.average,}})

                })

        .catch(
            (err => {
                console.log(err)
            }
            )
        )
    console.log(JSON.stringify(emner))
    updateEmner(emner)
    return emner
}